/* Cell phone size */
@media screen and (max-width:480px){
    .entire-display {
        /* border:1px solid blue; */
        margin-left:10%;
        margin-right:10%;
        margin-bottom:10%;
    }
    
    .library{
        display:flex;
        flex-direction:column;
        align-items:center;
        width:40%;
        /* border:1px solid orange; */
    }

    h2 {
        display:flex;
        justify-content:center;
        font-size:7vw;
        line-height:2vw;
    }
    }

/* Tablet size */
@media screen and (min-width:480px) and (max-width:768px){
    .entire-display {
        /* border:1px solid blue; */
        margin-left:5%;
        margin-right:5%;
        margin-bottom:10%;
    }
    
    .library{
        display:flex;
        flex-direction:column;
        align-items:center;
        width:40%;
        /* border:1px solid orange; */
    }

    h2 {
        display:flex;
        justify-content:center;
        font-size:7vw;
        line-height:1vw;
    }
}

/* Computer screen size */
@media screen and (max-width:2000px) and (min-width:768px){

    .entire-display {
        border:1px solid lightblue;
        height:30vw;
        padding:10px;
        margin-left:10%;
        margin-right:10%;
        margin-bottom:10%;
        display:flex;
        flex-direction:row;
    }
    
    .library{
        display:flex;
        flex-direction:column;
        align-items:center;
        width:33%;
        /* justify-content: space-between; */
        /* border:1px solid orange; */
    }

    h2 {
        display:flex;
        justify-content:center;
        font-size:4vw;
        line-height:1vw;
    }
}