@media screen and (max-width: 480px){
    .nav {
        display:flex;
        flex-direction:row;
    
    }


    .nav-logo {
        font-family: 'M PLUS Rounded 1c', sans-serif;
        font-size:5vw;
        color:whitesmoke;
        border:.5vw solid grey;
        border-radius:50%;
        padding:1vw;
        background-color:lightblue;
        margin: 1vw 1vw 1vw 1vw;
        /* color:red; */
    }

    .nav-links {
        font-size:3.5vw;

        color: whitesmoke;

        display:flex;
        flex-direction:row;
        justify-content:space-evenly;

        padding:2vw;
        background-color:lightblue;

        width:100%;
        height:100%;
    }

    .nav-style{
        color:black;
        text-decoration:none;
        font-size:2.5vw;
    }
}


